@import url("https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:wght@400;500;700&display=swap");
/*Custom typographies------------------------------*/
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul,
ol {
  list-style: none;
  padding: inherit;
}

html {
  padding: 0;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

body {
  height: 100vh;
  max-width: 1440px;
  text-rendering: optimizeSpeed;
  font-size: 100%;
  font-family: "Be Vietnam Pro", sans-serif;
}

/* A elements that don't have a class get default styles */
a, a:not([class]) {
  text-decoration-skip-ink: auto;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.primaryBtn {
  font-weight: 500;
  font-size: 0.8125rem;
  line-height: 1.1875rem;
  padding: 0.75rem 2rem 0.8125rem;
  color: white;
  background-color: hsl(12, 88%, 59%);
  border-radius: 1.375rem;
  box-shadow: 0px 15px 15px -10px #FF9F8E;
  cursor: pointer;
  transition: opacity ease 2ms;
}
.primaryBtn:hover {
  opacity: 0.5;
}

.secondaryBtn {
  font-weight: 500;
  font-size: 0.8125rem;
  line-height: 1.1875rem;
  padding: 0.75rem 2rem 0.8125rem;
  color: hsl(12, 88%, 59%);
  background-color: white;
  border-radius: 1.375rem;
  box-shadow: 0rem 0.9375rem 0.9375rem -0.625rem #DB5943;
}

.formBtn {
  font-weight: 700;
  font-size: 0.8125rem;
  line-height: 0.8125rem;
  padding: 0.875rem 1.5625rem 1.0625rem;
  background-color: hsl(12, 88%, 59%);
  color: hsl(0, 0%, 98%);
  border-radius: 1.375rem;
  border: none;
  width: 5rem;
  height: 2.75rem;
}

.navItem {
  font-weight: 500;
  font-size: 0.8125rem;
  line-height: 1.1875rem;
  color: hsl(228, 39%, 23%);
  display: flex;
  flex-direction: row;
  gap: 2rem;
}
.navItem li, .navItem a {
  color: inherit;
}

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.1875rem;
  padding-top: 3rem;
}
@media (min-width: 90rem) {
  .hero {
    flex-direction: row-reverse;
    gap: 5.625rem;
  }
}
@media (min-width: 90rem) {
  .hero > img {
    width: 33.75rem;
  }
}
.hero__content {
  text-align: center;
}
@media (min-width: 90rem) {
  .hero__content {
    text-align: left;
  }
}
.hero__content h1 {
  font-weight: 700;
  font-size: 2.5rem;
  line-height: 3.125rem;
  color: hsl(228, 39%, 23%);
  margin-bottom: 0.5625rem;
  letter-spacing: -0.0625rem;
}
@media (min-width: 90rem) {
  .hero__content h1 {
    font-weight: 700;
    font-size: 3.5rem;
    line-height: 4rem;
    margin-bottom: 1rem;
  }
}
.hero__content p {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.625rem;
  color: hsl(227, 12%, 61%);
  margin-bottom: 1.8125rem;
}
@media (min-width: 90rem) {
  .hero__content p {
    max-width: 21.875rem;
    margin-bottom: 2.5rem;
  }
}

.features {
  margin-top: 5.8125rem;
}
@media (min-width: 90rem) {
  .features {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 7.8125rem;
  }
}
.features__content {
  display: flex;
  flex-direction: column;
  gap: 0.8125rem;
  text-align: center;
  max-width: 20.1875rem;
}
@media (min-width: 90rem) {
  .features__content {
    text-align: left;
    max-width: none;
    width: 27.8125rem;
  }
}
.features__content > h2 {
  font-weight: 700;
  font-size: 1.875rem;
  line-height: 2.8125rem;
  color: hsl(228, 39%, 23%);
}
@media (min-width: 90rem) {
  .features__content > h2 {
    font-weight: 700;
    font-size: 2.5rem;
    line-height: 2.75rem;
    margin-bottom: 1.5rem;
    letter-spacing: -0.0444rem;
    flex-grow: 0;
  }
}
.features__content > p {
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.75rem;
  color: hsl(227, 12%, 61%);
}
@media (min-width: 90rem) {
  .features__content > p {
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.625rem;
    flex-grow: 1;
    flex-basis: 21.875rem;
  }
}
.features__list {
  margin-top: 3.375rem;
  width: calc(100vw - 3rem);
}
@media (min-width: 90rem) {
  .features__list {
    margin-top: 0;
    width: 33.75rem;
  }
}
.features__list-group {
  display: flex;
  flex-direction: column;
  gap: 2.9375rem;
}
@media (min-width: 90rem) {
  .features__list-group {
    max-width: 33.75rem;
    gap: 2.5rem;
  }
}
.features__list-group-items {
  counter-increment: countUp;
  position: relative;
}
.features__list-group-items::before {
  content: counter(countUp, decimal-leading-zero);
  display: inline-block;
  padding: 0.375rem 1.4375rem 0.625rem 1.5625rem;
  max-width: 4.1875rem;
  max-height: 2.4375rem;
  border-radius: 2.4375rem;
  background-color: hsl(12, 88%, 59%);
  color: white;
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.4375rem;
  text-align: center;
  position: absolute;
  inset: 0;
  margin-left: -0.5rem;
  margin-right: 0.9375rem;
}
@media (min-width: 90rem) {
  .features__list-group-items::before {
    margin-right: 1.75rem;
  }
}
.features__list-group-items > span {
  display: block;
  background-color: hsl(13, 100%, 96%);
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.4375rem;
  color: hsl(228, 39%, 23%);
  height: 2.4375rem;
  width: calc(100vw - 1.0625rem);
  padding-left: 4.875rem;
  padding-top: 0.375rem;
  padding-bottom: 0.625rem;
  margin-left: -0.4375rem;
  margin-bottom: 0.5rem;
}
@media (min-width: 90rem) {
  .features__list-group-items > span {
    margin-left: 0rem;
    background-color: transparent;
    width: 100%;
  }
}
.features__list-group-items > p {
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.75rem;
  color: hsl(227, 12%, 61%);
  margin-left: -0.5rem;
}
@media (min-width: 90rem) {
  .features__list-group-items > p {
    margin-left: 5rem;
    width: 27.8125rem;
  }
}

.convpanel {
  background-color: hsl(12, 88%, 59%);
  color: white;
  width: 100%;
  text-align: center;
  padding: 5.6875rem 1.9375rem 5.6875rem 2rem;
  background-image: url("/images/bg-simplify-section-mobile.svg");
  background-repeat: no-repeat;
  background-position: -0.9375rem;
}
@media (min-width: 90rem) {
  .convpanel {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 3.875rem 10.3125rem;
  }
}
.convpanel > h2 {
  font-size: 2.5rem;
  padding-bottom: 1.8125rem;
  letter-spacing: -0.0625rem;
  max-width: 19.5rem;
}
@media (min-width: 90rem) {
  .convpanel > h2 {
    padding-bottom: 0;
    font-size: 2.5rem;
    line-height: 2.75rem;
    font-weight: 700;
    max-width: 31.25rem;
    text-align: left;
  }
}

.testimonial {
  margin: 3.875rem 1.0625rem 2.625rem 1.125rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.testimonial > h2 {
  font-weight: 800;
  font-size: 2rem;
  line-height: 2.8125rem;
  color: hsl(228, 39%, 23%);
  margin-bottom: 2.25rem;
}
.testimonial__cards {
  margin-top: 3.875rem;
  position: relative;
}
.testimonial__cards__content {
  color: hsl(228, 39%, 23%);
  background-color: hsl(0, 0%, 98%);
  margin-bottom: 1.625rem;
  position: relative;
  display: none;
  max-width: 33.75rem;
  transition: display 2s ease-in-out;
}
.testimonial__cards__content--authpic {
  position: absolute;
  width: 4.5rem;
  height: auto;
  aspect-ratio: 1/1;
  top: -2.1875rem;
  left: 8rem;
}
.testimonial__cards__content--authname {
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.4375rem;
  padding-top: 3.75rem;
  padding-bottom: 1.1875rem;
}
.testimonial__cards__content--quote {
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.625rem;
  padding: 0px 1.3125rem 2.625rem;
  opacity: 0.5;
}
.testimonial__cards__content.active {
  display: block;
}
.testimonial__dots {
  margin-bottom: 2.9375rem;
}
.testimonial__dots--dot {
  display: inline-block;
  width: 0.4375rem;
  height: auto;
  aspect-ratio: 1/1;
  border-radius: 0.2188rem;
  border: 0.0625rem solid hsl(12, 88%, 59%);
  cursor: pointer;
  margin-right: 0.3125rem;
}
.testimonial__dots--dot.active {
  background-color: hsl(12, 88%, 59%);
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding-top: 2.5625rem;
}
@media (min-width: 90rem) {
  .header {
    padding-top: 3.5rem;
    color: hsl(228, 39%, 23%);
    background-image: url("/images/bg-simplify-section-desktop.svg");
  }
}
.header__logo {
  max-width: 6.875rem;
}
.header__nav__mobileNav > img {
  cursor: pointer;
}
.header__nav__mobileNav--menu {
  position: absolute;
  top: 0;
  left: -24px;
  z-index: 2;
  height: 100vh;
  width: 100vw;
  transition: display 0.3s ease;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0.33%, rgba(0, 0, 0, 0.599049) 99.79%);
}
.header__nav__mobileNav--menu > img {
  position: absolute;
  top: 38px;
  right: 26px;
  cursor: pointer;
}
.header__nav__mobileNav--menu-item {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin: 6.4375rem 1.625rem 0 1.5rem;
  padding: 2.5rem 0rem;
  text-align: center;
  background-color: white;
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.4375rem;
  color: hsl(228, 39%, 23%);
}
.header__nav__mobileNav--menu-item > li:hover {
  color: hsl(227, 12%, 61%);
}
@media (min-width: 90rem) {
  .header__nav .mobileNav {
    display: none;
  }
}
.header__nav .navItem {
  display: none;
}
@media (min-width: 90rem) {
  .header__nav .navItem {
    display: flex;
    flex-direction: row;
    font-weight: 500;
    font-size: 0.8125rem;
    line-height: 1.1875rem;
  }
}
.header__nav .navItem > li:hover {
  opacity: 0.5;
}
.header__cta {
  display: none;
}
@media (min-width: 90rem) {
  .header__cta {
    display: inline-block;
  }
}

.close {
  display: none;
}

.open {
  display: block;
}

.container {
  margin: 0 1.5rem;
}
@media (min-width: 90rem) {
  .container {
    margin-left: 10.3125rem;
    margin-right: 10.3125rem;
  }
}

