  font-size: 0.8125rem;
  line-height: 1.1875rem;
  padding-left: 1.5rem;
  border-radius: 1.375rem;
  height: 2.75rem;
  width: 14.9375rem;
  margin-right: 0.5rem;
}
.footer__column {
  display: flex;
  flex-direction: row;
  margin-top: 3.375rem;
  margin-bottom: 3.125rem;
  gap: 6.625rem;
  font-weight: 400;
  font-size: 0.9375rem;
  line-height: 1.375rem;
  color: white;
}
.footer__column > div > ul > li {
  margin-bottom: 0.8125rem;
}
.footer__social {
  display: flex;
  flex-direction: row;
  gap: 2.125rem;
  margin-bottom: 3.375rem;
}
.footer__social > img {
  aspect-ratio: 1/1;
  width: 2rem;
}
.footer__logo {
  margin-bottom: 3.125rem;
}
.footer__copyright {
  margin-bottom: 1.875rem;
  font-weight: 400;
  font-size: 0.8125rem;
  line-height: 1.1875rem;
  color: hsl(0, 0%, 98%);
  opacity: 0.5;
}

.flex {
  flex: 1 0 33.33%;
}

.flex:nth-child(-n+2) {
  flex-basis: 66.66%;
}